.shop-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.list {
  height: 100px;
  display: flex;
  justify-content: space-evenly;
  padding: 0px 80px;
  align-items: center;
  position: relative;
  background: #e0efe3;
}

.list::after {
  margin-top: 120px;
  content: "";
  width: 0px;
  height: 0px;
  position: relative;
  border-style: solid;
  border-width: 20px 20px 20px 0px;
  border-color: transparent #e0efe3 transparent transparent;
  display: inline-block;
  vertical-align: top;
  transform: rotate(-90deg);
  margin-right: 5px;
}

.extraItems {
  width: 35%;
  display: flex;
  justify-content: space-between;
}

.items {
  margin: auto;
  width: 90%;
  margin-top: 50px;
  gap: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.shop-item-divider::after {
  content: "";
  display: block;
  border-style: solid;
}

.shop-item-img {
  max-height: 320px !important;
  width: 100%;
  border-bottom: 4px solid rgb(46, 46, 46);
  border-radius: 20px;
  box-shadow: 8px 8px 2px rgba(0, 0, 0, 0.26);
}

.nothing-found-shop-text {
  color: grey;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.shop-search-bar button {
  align-items: center;
  margin-left: 5px;
}

.shop-item-img:hover {
  border-bottom: 4px solid #9b27b0;
  box-shadow: 8px 8px 2px #9b27b05d;
  /* transform: skew(120px); */
}
.extraLineBtn {
  margin: 10px 0px;
  text-align: center;
  position: relative;
}
@media all and (max-width: 900px) {
  .list::after {
    margin-left: 220px;
    position: absolute;
    margin-top: 215px;
  }
  .front-font {
    width: 100%;
  }
  .front-font h3 {
    font-size: 2em;
    text-align: center;
    margin: 20px 0px;
  }

  .items {
    margin: auto;
    width: 95%;
    margin-top: 50px;
    gap: 10px;
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .extraItems {
    width: 100%;
    display: flex;
    /* flex-direction: column; */
    justify-content: space-between;
  }
  .list {
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0px 5px;
    align-items: center;
    position: relative;
    background: #e0efe3;
  }
  .shop-search-bar {
    width: auto;
    height: auto !important;
    align-items: center;
    margin-left: 5px;
    display: flex;
  }

  .favoriteIconSHop {
    height: 0.8em !important;
    width: 0.8em !important;
  }
  .nothing-found-shop-text {
    font-size: 1.7em !important;
  }
}
