.hero-banner-cart {
  background-color: #e0efe3;
  text-align: center;
  padding: 25px 0 25px 0;
  position: relative;
}

.cart-div {
  margin: auto;
  width: 80%;
  margin-top: 50px;
}

.item-divider::after {
  content: "";
  display: block;
  border-style: solid;
}

.cart-item {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
  text-align: center;
  background-color: #e0efe3;
}

.item-border {
  width: 100%;
  height: 1px;
  display: block;
}

.cart-item-divider {
  margin: 0px;
}

.checkout-section {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
}

.checkout-section button {
  background-color: #00a000;
  color: white;
}

.checkout-section button:hover {
  background-color: white;
  color: #00a000;
}

.cart-item h3:nth-child(3) {
  display: flex;
  gap: 10px;
  align-items: center;
}

.cart-item h3:nth-child(2) {
  text-align: left;
}
.qty-btn {
  background-color: transparent !important;
  border: 1px solid rgb(61, 61, 61);
  border-radius: 10px;
}

.remove-btn-Cart :hover {
  background-color: red;
  color: white;
  height: 100%;
  padding: none;
}

@media all and (max-width: 900px) {
  .qty-btn {
    height: 1.3em !important;
    /* padding: 0px -100px !important; */
    width: 10em !important;

    /* display: none !important; */
  }
  .qty-btn-icon {
    height: 0.5em !important;
    width: 0.5em !important;
  }

  .cart-item {
    align-items: center !important;
    font-size: 0.7em !important;
  }

  .cart-item h3:nth-child(3) {
    display: flex;
    width: 10px !important;
    gap: 2px;
    align-items: center;
    /* background-color: black; */
  }
  .cart-div {
    margin: auto;
    width: 90%;
    margin-top: 50px;
  }
  .remove-btn-Cart {
    font-size: 1em !important;
  }
}
