.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: left;
}
.bg {
  margin-top: 40px;
  height: 550px;
  width: 550px;
  border: none;
  /* border-radius: 20em; */
}

.bg:hover {
  background-color: black;
}

.text {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.text button {
  margin-top: 10px;
  width: 120px;
}

@media all and (max-width: 900px) {
  .container {
    margin-top: 50%;
    flex-direction: column;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: left;
  }
  .bg {
    height: 300px;
    width: 300px;
    padding-left: 10vh;
    display: none;
  }
  .text {
    font-size: 1em;
    width: 90%;
    display: flex;
    flex-direction: column;
  }
  .text h2 {
    font-size: 4em;
  }
} ;
