@import url(https://fonts.googleapis.com/css2?family=Besley&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Besley", serif;
  box-sizing: border-box;
}

.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: left;
}
.bg {
  margin-top: 40px;
  height: 550px;
  width: 550px;
  border: none;
  /* border-radius: 20em; */
}

.bg:hover {
  background-color: black;
}

.text {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.text button {
  margin-top: 10px;
  width: 120px;
}

@media all and (max-width: 900px) {
  .container {
    margin-top: 50%;
    flex-direction: column;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: left;
  }
  .bg {
    height: 300px;
    width: 300px;
    padding-left: 10vh;
    display: none;
  }
  .text {
    font-size: 1em;
    width: 90%;
    display: flex;
    flex-direction: column;
  }
  .text h2 {
    font-size: 4em;
  }
} ;

.shop-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.list {
  height: 100px;
  display: flex;
  justify-content: space-evenly;
  padding: 0px 80px;
  align-items: center;
  position: relative;
  background: #e0efe3;
}

.list::after {
  margin-top: 120px;
  content: "";
  width: 0px;
  height: 0px;
  position: relative;
  border-style: solid;
  border-width: 20px 20px 20px 0px;
  border-color: transparent #e0efe3 transparent transparent;
  display: inline-block;
  vertical-align: top;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  margin-right: 5px;
}

.extraItems {
  width: 35%;
  display: flex;
  justify-content: space-between;
}

.items {
  margin: auto;
  width: 90%;
  margin-top: 50px;
  grid-gap: 10px;
  gap: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.shop-item-divider::after {
  content: "";
  display: block;
  border-style: solid;
}

.shop-item-img {
  max-height: 320px !important;
  width: 100%;
  border-bottom: 4px solid rgb(46, 46, 46);
  border-radius: 20px;
  box-shadow: 8px 8px 2px rgba(0, 0, 0, 0.26);
}

.nothing-found-shop-text {
  color: grey;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.shop-search-bar button {
  align-items: center;
  margin-left: 5px;
}

.shop-item-img:hover {
  border-bottom: 4px solid #9b27b0;
  box-shadow: 8px 8px 2px #9b27b05d;
  /* transform: skew(120px); */
}
.extraLineBtn {
  margin: 10px 0px;
  text-align: center;
  position: relative;
}
@media all and (max-width: 900px) {
  .list::after {
    margin-left: 220px;
    position: absolute;
    margin-top: 215px;
  }
  .front-font {
    width: 100%;
  }
  .front-font h3 {
    font-size: 2em;
    text-align: center;
    margin: 20px 0px;
  }

  .items {
    margin: auto;
    width: 95%;
    margin-top: 50px;
    grid-gap: 10px;
    gap: 10px;
    display: flex;
    flex-direction: column;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .extraItems {
    width: 100%;
    display: flex;
    /* flex-direction: column; */
    justify-content: space-between;
  }
  .list {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0px 5px;
    align-items: center;
    position: relative;
    background: #e0efe3;
  }
  .shop-search-bar {
    width: auto;
    height: auto !important;
    align-items: center;
    margin-left: 5px;
    display: flex;
  }

  .favoriteIconSHop {
    height: 0.8em !important;
    width: 0.8em !important;
  }
  .nothing-found-shop-text {
    font-size: 1.7em !important;
  }
}

.appbar {
  display: flex;
  height: 80px;
  justify-content: center;
}

.logo {
  text-decoration: none;
  color: white;
}

.Toolbar {
  justify-content: space-around;
  align-items: center;
}

.links {
  color: white;
  margin: 0px 10px;
  /* position: absolute; */
}

.shoppingCartIcon {
  height: 2rem !important;
  width: 2rem !important;
}

.notification {
  position: absolute;
  background-color: red;
  border-radius: 20px;
  height: 25px;
  width: 25px;
  top: -2px;
  left: 35px;
  color: white;
}

@media all and (max-width: 900px) {
  .Toolbar {
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
  }
  .logo {
    flex-grow: 2;
  }
  .logo h4 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 1.2em;
    font-style: italic;
    -webkit-text-decoration: underline red;
            text-decoration: underline red;
  }
  .links-wrapper {
    display: flex;
    justify-content: space-around;
    flex-grow: 1;
    width: -webkit-max-content;
    width: max-content;
  }
  .links button {
    font-size: 1em;
  }
  .shoppingCartIcon {
    height: 1.4rem !important;
    width: 1.4rem !important;
  }
  .themeIcon {
    height: 1.4rem !important;
    width: 1.4rem !important;
  }
}

.product-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.product-info {
  /* flex-grow: 2; */
  width: 610px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.product-info button {
  width: 200px;
  position: relative;
}

.product-display-img {
  /* flex-grow: 4; */
  margin-top: 70px;
  border: 2px solid black;
  box-shadow: 4px 4px rgba(0, 0, 0, 0.37);
}

.product-display-img img {
  height: 500px;
  width: 500px;
}

.item-buy-popup {
  display: flex;
  align-items: center;
}

.item-popup {
  margin-left: 5px;
  position: relative;
  background-color: yellow;
  padding: 5px 10px;
}

.item-popup::before {
  content: "";
  width: 0px;
  height: 0px;
  left: -13px;
  position: absolute;
  border-style: solid;
  border-width: 13px 13px 15px 0px;
  border-color: transparent yellow transparent transparent;
  display: block;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  margin-right: 5px;
}

@media all and (max-width: 900px) {
  .product-wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column-reverse;
    width: 90%;
  }
  .product-display-img img {
    height: 300px;
    width: 300px;
  }
  .product-info {
    margin-top: 30px;
    width: 310px;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
  }
  .product-info button {
    width: 200px;
    position: relative;
    margin-bottom: 70px;
  }
  .item-popup {
    margin-left: 5px;
    position: relative;
    background-color: yellow;
    padding: auto auto;
    margin-top: -55px;
    width: -webkit-max-content;
    width: max-content;
  }
  .item-popup::before {
    content: "";
    width: 0px;
    height: 0px;
    left: -13px;
    position: absolute;
    border-style: solid;
    border-width: 13px 13px 15px 0px;
    border-color: transparent yellow transparent transparent;
    display: block;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    margin-right: 5px;
  }
}

.hero-banner-cart {
  background-color: #e0efe3;
  text-align: center;
  padding: 25px 0 25px 0;
  position: relative;
}

.cart-div {
  margin: auto;
  width: 80%;
  margin-top: 50px;
}

.item-divider::after {
  content: "";
  display: block;
  border-style: solid;
}

.cart-item {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
  text-align: center;
  background-color: #e0efe3;
}

.item-border {
  width: 100%;
  height: 1px;
  display: block;
}

.cart-item-divider {
  margin: 0px;
}

.checkout-section {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
}

.checkout-section button {
  background-color: #00a000;
  color: white;
}

.checkout-section button:hover {
  background-color: white;
  color: #00a000;
}

.cart-item h3:nth-child(3) {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: center;
}

.cart-item h3:nth-child(2) {
  text-align: left;
}
.qty-btn {
  background-color: transparent !important;
  border: 1px solid rgb(61, 61, 61);
  border-radius: 10px;
}

.remove-btn-Cart :hover {
  background-color: red;
  color: white;
  height: 100%;
  padding: none;
}

@media all and (max-width: 900px) {
  .qty-btn {
    height: 1.3em !important;
    /* padding: 0px -100px !important; */
    width: 10em !important;

    /* display: none !important; */
  }
  .qty-btn-icon {
    height: 0.5em !important;
    width: 0.5em !important;
  }

  .cart-item {
    align-items: center !important;
    font-size: 0.7em !important;
  }

  .cart-item h3:nth-child(3) {
    display: flex;
    width: 10px !important;
    grid-gap: 2px;
    gap: 2px;
    align-items: center;
    /* background-color: black; */
  }
  .cart-div {
    margin: auto;
    width: 90%;
    margin-top: 50px;
  }
  .remove-btn-Cart {
    font-size: 1em !important;
  }
}

