.appbar {
  display: flex;
  height: 80px;
  justify-content: center;
}

.logo {
  text-decoration: none;
  color: white;
}

.Toolbar {
  justify-content: space-around;
  align-items: center;
}

.links {
  color: white;
  margin: 0px 10px;
  /* position: absolute; */
}

.shoppingCartIcon {
  height: 2rem !important;
  width: 2rem !important;
}

.notification {
  position: absolute;
  background-color: red;
  border-radius: 20px;
  height: 25px;
  width: 25px;
  top: -2px;
  left: 35px;
  color: white;
}

@media all and (max-width: 900px) {
  .Toolbar {
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
  }
  .logo {
    flex-grow: 2;
  }
  .logo h4 {
    width: fit-content;
    font-size: 1.2em;
    font-style: italic;
    text-decoration: underline red;
  }
  .links-wrapper {
    display: flex;
    justify-content: space-around;
    flex-grow: 1;
    width: max-content;
  }
  .links button {
    font-size: 1em;
  }
  .shoppingCartIcon {
    height: 1.4rem !important;
    width: 1.4rem !important;
  }
  .themeIcon {
    height: 1.4rem !important;
    width: 1.4rem !important;
  }
}
