.product-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.product-info {
  /* flex-grow: 2; */
  width: 610px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.product-info button {
  width: 200px;
  position: relative;
}

.product-display-img {
  /* flex-grow: 4; */
  margin-top: 70px;
  border: 2px solid black;
  box-shadow: 4px 4px rgba(0, 0, 0, 0.37);
}

.product-display-img img {
  height: 500px;
  width: 500px;
}

.item-buy-popup {
  display: flex;
  align-items: center;
}

.item-popup {
  margin-left: 5px;
  position: relative;
  background-color: yellow;
  padding: 5px 10px;
}

.item-popup::before {
  content: "";
  width: 0px;
  height: 0px;
  left: -13px;
  position: absolute;
  border-style: solid;
  border-width: 13px 13px 15px 0px;
  border-color: transparent yellow transparent transparent;
  display: block;
  transform: rotate(0deg);
  margin-right: 5px;
}

@media all and (max-width: 900px) {
  .product-wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column-reverse;
    width: 90%;
  }
  .product-display-img img {
    height: 300px;
    width: 300px;
  }
  .product-info {
    margin-top: 30px;
    width: 310px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .product-info button {
    width: 200px;
    position: relative;
    margin-bottom: 70px;
  }
  .item-popup {
    margin-left: 5px;
    position: relative;
    background-color: yellow;
    padding: auto auto;
    margin-top: -55px;
    width: max-content;
  }
  .item-popup::before {
    content: "";
    width: 0px;
    height: 0px;
    left: -13px;
    position: absolute;
    border-style: solid;
    border-width: 13px 13px 15px 0px;
    border-color: transparent yellow transparent transparent;
    display: block;
    transform: rotate(0deg);
    margin-right: 5px;
  }
}
